import React from 'react';
import './App.css';
import styled from 'styled-components';

import {
  Button,
  UncontrolledTooltip,
} from "reactstrap";

import SkillSection from './skillSection';
import { Section, Container, Tint } from './layout';
import { darkGrey } from './theme';

function HeroSection(props: any) {
  return <Section style={props.style}>
    <Tint 
      style={{
        display: 'flex',
        alignItems: 'center',
        
      }}
      color="#15141452">
        {props.children}
    </Tint>
  </Section>
}
 
function App() {

  return (
    <>
    <HeroSection style={{
      justifyContent: 'center',
      alignItems: 'center',
      color: 'white',
      backgroundImage: 'url(' + require("./assets/DSC05102.JPG") + ')',
      backgroundSize: 'cover',
      backgroundPosition: 'center'
      
    }}>
      <div style={{
          width: '100%',
          backgroundColor: '#ffffff61',
          padding: '32px',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          flexFlow: 'column nowrap',
          }}>
          <div>
        
          <h1 style={{fontWeight: 800, fontSize: '6rem'}}>BENJAMIN BROMLEY</h1>
          <h2 className="subheading" style={{textAlign: 'right'}}>
            <i>Software Engineer</i>
          </h2>
        </div>
      </div>
    </HeroSection>
    <Section>
      <Container>
        <h2 style={{textAlign: 'center'}}>My Skills</h2>
        <SkillSection/>
      </Container>
    </Section>

    <Container>
      <h1 style={{textAlign: 'center'}}>TO BE CONTINUED...</h1>
    </Container>
    </>
  );
}

export default App;
