import React from 'react';
import styled from 'styled-components';

const Section = styled.section`
    height: 100vh;
`;

const Tint = styled.div`
    height: 100%;
    width: 100%;
    background-color: ${props => props.color};
`;

const Container = styled.div`
    max-width: 1200px;
    margin: auto;
    padding: 16px;
    padding-top: 32px;
`;

